@import url("https://fonts.googleapis.com/css?family=Lobster&display=swap");

.svgImg {
  padding-left: 32px;
}

.svg-mob {
  padding-top: 15px;
}

.text-white {
  color: white;
}

.background-image {
  width: 156px;
  height: 100%;
}

.background-image-mob {
  width: 150px;
  height: 180px;
}

.instruction-img {
  padding-left: 20px;
  width: 500px;
  height: 212px;
  display: flex;
}
.instruction-img-mob {
  padding-top: 20px;
  height: 180px;
  display: flex;
}

.text-in-circle {
  color: rgb(255, 255, 255);
  width: 300px;
  font-style: normal;
  line-height: 1.25;
  text-align: center;
  right: 62px;
}
.margin-left-120 {
  margin-left: 120px;
}
.padding-right-2 {
  padding-right: 2px;
}

.padding-top-5 {
  padding-top: 5px;
}
.text-color-beige {
  color: #e9ecef;
}
.text-color-goldenrod {
  color: goldenrod;
}
.text-color-cadetblue {
  color: cadetblue;
}

.padding-bottom-10 {
  padding-bottom: 10px;
}

.font-style-lobster {
  font-family: Lobster, "Franklin Gothic Medium", "Arial Narrow", Arial,
    sans-serif;
}

.font-style-monospace {
  font-family: monospace, "Franklin Gothic Medium", "Arial Narrow", Arial,
    sans-serif;
}

ol {
  padding-left: 20px;
}

li:hover {
  cursor: pointer;
}

.ol_mobile {
  padding-left: 5px;
}

.container {
  box-sizing: content-box;
  display: inline;
  float: none;
  line-height: 30px;
  position: static;
  z-index: auto;
  border: groove;
  padding: 0px;
  margin: 3px;
  margin-left: 15px;
  height: 150px;
  border-color: darkblue;
  background-color: white;
}

.container_mobile {
  padding-left: 15px;
  line-height: 20px;
}
